.container {
  @apply flex flex-col w-full h-full;

  button {
    @apply p-1 pl-2 pr-2 bg-gray-300 mb-1 mt-1 border border-gray-600 active:bg-gray-400 disabled:active:bg-gray-300 disabled:opacity-30 whitespace-nowrap transition-all;
  }

  .restart {
    @apply bottom-1 left-2 absolute;
  }
}